.about {
    &__content {
        display: flex;
        gap: 20px;
        // padding: 20px;
        width: 100%;

        &__personalWrapper,
        &__servicesWrapper {
            width: 50%;
            align-content: center;
            
        }

        &__personalWrapper {

            // border-style: dotted;
            padding-left: 50px;
            h3 {
                margin: 10px;
                font-size: 4rem;
                font-weight: 700;
                padding-left: 15px;
                color: var(--yellow-theme-main-color);
            }
            
            h3.personalInformation {
                padding-top: 10px;
                padding-left: 20px;
                font-size: 40px;
            }

            p{
                font-size: 20px;
                width: 80%;
                letter-spacing: 1px;
                padding-left: 20px;
                line-height: 1.65;
                padding-bottom: 20px;
                border-bottom: 1px dashed var(--yellow-theme-main-color);
            }

            ul > li{
                line-height: 1 rem;
                margin-bottom: 5px;
                font-size: 2rem;
                padding-top: 5px;
                padding-left: 50px;

                span.title, span.value{ 
                font-size: 4rem;
                font-weight: 700;
                letter-spacing: 0.3rem;
                border-bottom: 2px solid var(--yellow-theme-main-color);
                position:relative;
                display: inline-block;
                padding-bottom: 1px;
                color: var(--yellow-theme-sub-text-color);
                padding-left: 50px;
                }

                span.value{
                    border: none;
                }
            }
        }

        &__servicesWrapper{

            &__inner{
                
                width: 500px;
                height: 500px;
                // background: radial-gradient(180px, rgb(8, 2, 2), transparent 90%);
                position: relative;
                // border-radius: 50%;
                // border: 1.5px solid var(--yellow-theme-main-color);
                animation: oscillate 4s infinite;
                align-items: center;
                                

                div:nth-child(1){
                    position: relative;
                    padding-bottom: 20px;
                    padding-top: 20px;
                    padding-left: 80%;
                }
                div:nth-child(2){
                    position: relative;
                    padding-bottom: 20px;
                    padding-top: 20px;
                    padding-left: 60%;
                }
                div:nth-child(3){
                    position: relative;
                    padding-bottom: 20px;
                    padding-top: 20px;
                    padding-left: 40%;
                }
                div:nth-child(4){
                    position: relative;
                    padding-bottom: 20px;
                    padding-top: 20px;
                    padding-left: 20%;
                }
            }
        }
    }
}


@keyframes oscillate {
    from {
        width: 0%;
   }
   to{
       width:100%;
   }

}