.resume {
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    padding-top: 5%;
    .education {
        padding-top: 10px;
        padding-bottom: 10px;
        text-align: center;
        color: rgb(241, 230, 13);
        background-color: rgb(73, 73, 69);

        &__header {
            h1 {
                font-size: 50px;
            }

            span {
                color: var(--yellow-main-theme-color);
                text-align: center;
            }
        }

        &__content {
            &__rowitem {
                font-size: 30px;

                &___first {
                    h3 {
                        color: rgb(240, 252, 11);
                        font-size: 40px;
                    }
                }
            }
        }
    }

    .work {
        text-align: center;
        padding-top: 20px;
        padding-bottom: 20px;

        &__header {

            h1 {
                color: rgb(11, 11, 11);
                font-size: 50px;
            }
        }

        &__content {

            color: rgb(218, 233, 10);

            &__company {

                &__name {
                    h1 {
                        font-size: 30px;
                    }

                    p {
                        font-size: 25px;
                    }
                }
            }

        }
    }

    .skills {

        text-align: center;
        background-color: rgb(87, 87, 83);
        padding-top: 20px;
        padding-bottom: 20px;

        &__header {

            h1 {
                font-size: 50px;
            }
        }

        &__content {

            &__bars {

                ul>li {
                    color: rgb(217, 239, 15);
                    font-size: 20px;
                }
            }


        }
    }

}