.item-wrap{
    display: flex;
    justify-content: space-around;
    width: 100%;
    .item-img{
        padding-top: 10px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 10px;

        .image-item img {
            width: 50%;
            height: 50%;
            object-fit: cover;
          }

    }

    .overlay{
        position: relative;
        padding-top: 10px;
        padding-left: 200px;
        padding-right: 10px;
        padding-bottom: 10px;

        .portfolio-item-meta{

            h5{

                font-size: 2rem;
            }
        }
    }
}


