.contact {
    padding-left: 20px;
    padding-top: 5px;
    padding-bottom: 20px;
    padding-right: 10px;
    background-color: rgb(183, 183, 169);
    justify-content: center;

    &__header {

        h1 {
            font-size: 30px;
            background-color: rgb(183, 183, 169);
            padding-bottom: 20px;
            padding-top: 10px;
        }
    }

    &__main {

        &__template {

            border-style: dashed;
            border-color: black;
            padding-left: 20px;
            padding-top: 20px;
            padding-bottom: 20px;
            padding-right: 20px;
            background-color: rgb(183, 183, 169);

            &__form {

                &__fieldset {

                    border-color: black;
                    border-style:none;

                    &__name {

                        padding-bottom: 5px;
                        padding-left: 10px;
                        padding-top: 5px;

                        &__label {

                            font-size: 2.6rem;
                            color: rgb(17, 17, 18);
                        }

                        &__value {

                            width: 40%;
                            padding-bottom: 5px;
                            padding-left: 10px;

                        }
                    }



                    &__email {

                        padding-bottom: 5px;
                        padding-left: 10px;
                        padding-top: 5px;
                        &__label {

                            font-size: 2.6rem;
                            color: rgb(12, 12, 12);
                        }

                        &__value {

                            width: 40%;
                            padding-bottom: 5px;
                            padding-left: 10px;
                        }
                    }


                    &__subject {

                        padding-bottom: 10px;
                        padding-left: 10px;
                        padding-top: 5px;

                        &__label {

                            font-size: 2.6rem;
                            color: rgb(17, 17, 18);
                        }

                        &__value {

                            width: 40%;
                            padding-bottom: 10px;
                            padding-left: 10px;

                        }
                    }


                    &__message {

                        padding-bottom: 5px;
                        padding-left: 10px;
                        padding-top: 5px;
                        padding-right: 10px;

                        &__label {

                            font-size: 2.6rem;
                            color: rgb(17, 17, 18);
                        }

                        &__value {
                            height: 30%;
                            width: 100%;
                            padding-bottom: 5px;
                            padding-left: 10px;

                        }
                    }

                    &__submit {

                        padding-left: 50%;
                        padding-bottom: 5px;

                        button {
                            background-color: rgb(149, 149, 137);
                            font-size: large;
                        }
                    }
                }
            }
        }


        &__bottom {

            display: flex;

            align-content: center;
            padding-bottom: 10px;
            justify-content: center;

            ul {
                display: flex;
                list-style: none;
                padding: 0;
                justify-content: center;
            }

            li {
                margin-right: 10px;
                padding: 5px;
            }



        }
    }
}