@import url('https://fonts.google.com/specimen/Roboto');


:root {

    --yellow-theme-background-color: #51514c;
    --yellow-theme-main-color: #ceb80d;
    --yellow-theme-sub-text-color: #5b5b56;
    --yellow-theme-nav-background-color: #1b1717;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

html {
    font-size: 60%;
    height: 100%;
}

body {
    background: var(--yellow-theme-background-color);
    height: 100%;
}

#root {
    height: 100%;
}